import {
	UPLOAD_FILE_URL
} from '../../utils/api.js';
import {
	dateFormat
} from '../../utils/dateFormat.js';
export default {
	data() {
		return {
			index: 0,
			picker: ['餐饮券', '影音娱乐券', '景点门票', '电影票', '演唱会门票', '展览门票'],
			radio: 'SELL', // S:出售 B:求购 进入画面出售按钮处于Checked状态
			startDate: dateFormat('YYYY-mm-DD', new Date()),
			endDate: dateFormat('YYYY-mm-DD', new Date(new Date().getTime() + 24 * 60 * 60 * 1000)),
			imgList: [],
			ticketType: 'FOOD',
			ticketName: '',
			qrCode: '',
			price: '',
		}
	},
	methods: {
		RadioChange: function(e) {
			this.radio = e.detail.value;
		},
		PickerChange: function(e) {
			this.index = e.detail.value;
			switch (index) {
				case 0:
					ticketType = 'FOOD'; // 餐饮券
					break;
				case 1:
					ticketType = 'ENTERTAINMENT'; // 影音娱乐券
					break;
				case 2:
					ticketType = 'ADMINSION'; // 景点门票
					break;
				case 3:
					ticketType = 'FILM'; // 电影票
					break;
				case 4:
					ticketType = 'CONCERT'; // 演唱会门票
					break;
				case 5:
					ticketType = 'EXHIBITION'; // 展览门票
					break;
				default:
					ticketType = 'FOOD';
					break;
			};
		},
		GetPrice: function(e) {
			this.price = e.detail.value
				.replace(/[^\d.]/g, '') // 清除“数字”和“.”以外的字符
				.replace(/\.{2,}/g, '.') // 只保留第一个. 清除多余的
				.replace('.', '$#$')
				.replace(/\./g, '')
				.replace('$#$', '.')
				.replace(/^(\-)*(\d+)\.(\d\d).*$/, '$1$2.$3'); // 只能输入两个小数
			if (this.price.indexOf('.') < 0 && this.price != '') {
				// 以上已经过滤，此处控制的是如果没有小数点，首位不能为类似于 01、02的金额
				this.price = parseFloat(this.price);
			};
			return {
				value: this.price,
			};
		},
		GetQrCode: function(e) {
			// 限制二维码串只能输入英文和数字
			this.qrCode = e.detail.value.replace(/[^\a-\z\A-\Z0-9]/g, '');
			return {
				value: this.qrCode,
			};
		},
		GetTypeName: function(e) {
			this.ticketType = e.detail.value;
		},
		StartDateChange: function(e) {
			this.startDate = e.detail.value;
		},
		EndDateChange: function(e) {
			this.endDate = e.detail.value;
		},
		ChooseImage: function(e) {
			uni.chooseImage({
				count: 4, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: (res) => {
					const tempFilePaths = res.tempFilePaths;
					if (this.imgList.length != 0) {
						this.imgList = this.imgList.concat(tempFilePaths);
					} else {
						this.imgList = tempFilePaths;
					}
				}
			});
		},
		ViewImage: function(e) {
			uni.previewImage({
				urls: this.imgList,
				current: e.currentTarget.dataset.url
			});
		},
		DelImg: function(e) {
			uni.showModal({
				content: '确定要删除这张二维码截图吗？',
				confirmText: '确认',
				cancelText: '取消',
				success: res => {
					if (res.confirm) {
						this.imgList.splice(e.currentTarget.dataset.index, 1)
					}
				}
			})
		},
		CommitEticket: function(e) {
			let user = uni.getStorageSync('user');
			let wechatOpenId = user.wechatOpenId;
			let telephoneNumber = user.telephoneNumber;
			var count = 0;
			var imgList = this.imgList;
			for (var i = 0; i < imgList.length; i++) {
				uni.uploadFile({
					url: UPLOAD_FILE_URL,
					filePath: imgList[i],
					name: 'uploadFile',
					formData: {
						'wechatOpenId': wechatOpenId,
						'ticketType': this.ticketType
					},
					header: {
						"Content-Type": "multipart/form-data",
						'X-Token': uni.getStorageSync('token').token
					},
					success: function(uploadFileRes) {
						count++;
						if (count == imgList.length && uploadFileRes.res == '100') {
							console.log('成功');
						}
					},
					fail: function(res) {
						uni.showModal({
							title: '错误',
							content: '二维码图片上传失败，请再次点击确认！',
							showCancel: false
						})
					}
				});
			}
		}
	}
}
